



















import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';
import store from '@/store';

@Component
export default class Tags extends Vue {
  @Prop(Object) readonly selectedTag!: Tag;

  setSelectedTag({icon, name}: Tag) {
    this.$emit('update:selectedTag', {icon, name});
  }

  created() {
    store.commit('fetchTagList');
  }

  get tagList() {
    return this.type === '-' ? store.state.tagListOut[0] : store.state.tagListIn[0];
  }


  get type() {
    return store.state.type;
  }

}
